  color: #00755E;
}

#footer .text-center p a:hover {
  color: #b30015;
}

#footer .copyright {
  font-size: 16px;
  margin-bottom: 0px;
  padding-bottom: 0;
}

.social {
  padding: 0;
  margin: 0;
  display: inline-block;
  position: relative;
  width: 100%;
}

.social li {
  list-style: none;
  padding: 0;
  margin: 0;
  display: inline-block;
}

.social li a {
  font-size: 30px;
  display: table;
  width: 50px;
  height: 50px;
  margin: 0 10px;
}

.social li a i {
  display: table-cell;
  vertical-align: middle;
  color: #032B52;
}

.social li a:hover {
  background: #5FB3AC;
  text-decoration: none;
}

.social.social-box a {
  background: #F7F5FA;
  color: #032B52;
}

.social.social-circle a {
  background: #F7F5FA;
  color: #032B52;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
}

@media screen and (max-width: 600px) {
  #footer {
    padding: 1px 0 1px 0;
    margin: 0 auto;
  }
}
