@font-face{
    font-family:Graphik-Semibold;
    src:url(fonts/semibold/Graphik-Semibold-Web.eot);
    src:url(fonts/semibold/Graphik-Semibold-Web.eot?#iefix) format("embedded-opentype"),
    url(fonts/semibold/Graphik-Semibold-Web.woff) format("woff"),
    url(fonts/semibold/Graphik-Semibold-Web.ttf) format("truetype"),
    url(fonts/semibold/Graphik-Semibold-Web.svg#Graphik-Semibold) format("svg");
    font-weight:600;
    font-style:normal;
    font-stretch:normal
}
@font-face{
    font-family:Graphik-Medium;
    src:url(fonts/medium/Graphik-Medium-Web.eot);
    src:url(fonts/medium/Graphik-Medium-Web.eot?#iefix) format("embedded-opentype"),
    url(fonts/medium/Graphik-Medium-Web.woff) format("woff"),
    url(fonts/medium/Graphik-Medium-Web.ttf) format("truetype"),
    url(fonts/medium/Graphik-Medium-Web.svg#Graphik-Medium) format("svg");
    font-weight:500;
    font-style:normal;
    font-stretch:normal
}
@font-face{
    font-family:Graphik-Regular;
    src:url(fonts/regular/Graphik-Regular-Web.eot);
    src:url(fonts/regular/Graphik-Regular-Web.eot?#iefix) format("embedded-opentype"),
    url(fonts/regular/Graphik-Regular-Web.woff) format("woff"),
    url(fonts/regular/Graphik-Regular-Web.ttf) format("truetype"),
    url(fonts/regular/Graphik-Regular-Web.svg#Graphik-Regular) format("svg");
    font-weight:400;
    font-style:normal;
    font-stretch:normal
}